<template>
    <v-container class="pa-0" style="position: relative;">
        <v-app-bar
                app
                color="#FFFFFF"
                elevation="3"
                class="app-bar-gradient-back"
                dark
        >
            <v-row>
                <v-col class="text-left pa-0">
                    <v-btn text icon dark
                           tile
                           height="100%"
                           width="50px"
                           @click="$router.go(-1)">

                        <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>
                </v-col>
                <v-col class="text-center">
                    <v-toolbar-title>{{accountText}}</v-toolbar-title>
                </v-col>
                <v-col>
                </v-col>
            </v-row>
        </v-app-bar>

        <v-card style="margin-top: 68px;">
            <v-list active-class="primary--text" style="padding: 0px!important" >
                <v-list-item @click="$router.push({name: 'settingsAccountEditNameAndLastName'})" two-line>
                    <v-list-item-content>
                        <v-list-item-title>{{nameAndLastNameText}}</v-list-item-title>
                        <v-list-item-subtitle>{{name}} {{lastName}}</v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>

                <v-divider></v-divider>

                <v-list-item @click="$router.push({name: 'settingsAccountPasswordVerification'})" two-line>
                    <v-list-item-content>
                        <v-list-item-title>{{emailText}}</v-list-item-title>
                        <v-list-item-subtitle>{{email}}</v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>

                <v-divider></v-divider>

                <v-list-item @click="$router.push({name: 'settingsAccountEditPassword'})" two-line>
                    <v-list-item-content>
                        <v-list-item-title>{{passwordText}}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-card>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    export default {
        name: "SettingsAccount",
        data() {
            return {
                snackbar: false,
                snackbarText: null,
                name: this.$store.getters['user/getName'],
                lastName: this.$store.getters['user/getLastName'],
                email: this.$store.getters['user/getEmail'],
                passwordText: null,
                closeText: null,
                accountText: null,
                nameAndLastNameText: null,
                emailText: null
}
        },
        created() {
            this.passwordText = this.$i18n.t('messages.TEXT_PASSWORD');
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.accountText = this.$i18n.t('messages.TEXT_ACCOUNT');
            this.nameAndLastNameText = this.$i18n.t('messages.TEXT_NAME_AND_LASTNAME');
            this.emailText = this.$i18n.t('messages.TEXT_EMAIL_DIRECTION');
        }
}
</script>

<style scoped>

</style>